@charset "utf-8";

// Import fonts and define font variables
@import url(https://fonts.googleapis.com/css?family=Lato:300);
$font-family-sans-serif: 'Lato';

// Set our own variables
$aspa-red-color: rgb(246, 0, 46);
$aspa-gray-color: rgb(164, 163, 164);

$base-font-size: 20px;
$small-font-size: $base-font-size * 0.875;
$spacing-unit: 30px;
$grey-color: #828282;

// Override Bootstrap variables
$font-size-base: 20px;
$navbar-height: 50px;
$navbar-inverse-bg: $aspa-red-color;
$navbar-inverse-link-active-bg: $aspa-red-color;
$navbar-inverse-link-color: #FFF;
$navbar-inverse-link-hover-color: lighten($aspa-gray-color, 30%);
$navbar-inverse-brand-hover-color: lighten($aspa-gray-color, 30%);
$link-hover-decoration: none;

// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "variables",
        "bootstrap",
        "bootswatch",
        "aspa";
