.tagline {
  font-size: 24px;
  text-align: center;
  margin-top: 32px;
}

.content-wrapper .container {
  max-width: 730px;
}

body {
  padding-top: $navbar-height;
}

h1 {
  font-size: 42px;
  letter-spacing: -1px;
  line-height: 1;
}

img {
  max-width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
}

img.aspa-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

img.speaker-photo {
  @extend .thumbnail;
  @extend .pull-left;
  margin-right: 20px;
  margin-bottom: 0px;
}

// Pagination

.back-to-news {
  @extend .pager;
}

.pager {
  font-size: $small-font-size;
}

// Page content

.page-content {
  padding: $spacing-unit 0;
}

.page-heading {
  font-size: 20px;
}

.post-list {
  margin-left: 0;
  padding-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  font-size: 24px;
}

// Posts

.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  font-size: 42px;
  letter-spacing: -1px;
  line-height: 1;
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 26px;
  }

  h4 {
    font-size: 20px;
  }
}

footer div.container {
  font-size: 16px;
}
